* {
    font-family: Helvetica, Arial, sans-serif;
}

body {
    padding: 60px;
}

input {
    color: grey;
    width: 100%;
    font-size: 1.5rem;
}

 .card-grid {
     display: flex;
     flex-direction: row;
     flex-wrap: wrap;  

 }

 .card {
     align-content: center;
     border: 1px solid grey;
     border-radius: 6px;
     margin-bottom: 10px;
     margin-right: 10px;
     overflow: hidden;
     

 }

 .card p {
     padding: 1px;
     text-align: center; 
     text-align-last: auto;    
 }

 .card img{
     max-height: 170px;    
 }